}
.projectCard{
    display: flex;
    justify-content: space-around;
    border: 2px solid black; 
    border-radius: 20px;
    margin-top: 50px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.projDescription{
    width: 45%;
    padding-top: 40px;
}
.projDescription h1{
    font-size: 50px;
    font-weight: 600;    text-align: left;

}
.projDescription h2{
    font-size: 30px;
    font-weight: 500;
    margin-top: 30px;    text-align: left;


}
.prodescbtnbox1{
    margin-left: -130px;
}
.prodescbtnbox2{
    margin-left: -315px;
}
.projdebtn{
    border: none;
    background-color: black;
    color: white;
    font-size: 16px;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 4px;   
   
}
.projDescription h3{
    font-size: 21px;
    font-weight: 400;
    text-align: left;

}
.secpro{
    width: 450px;
    height: 450px;
    overflow: hidden;
    margin: 50px auto;
    border: 5px solid black;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;}
.secpro:hover img{
    object-position: bottom center;
}
.projimg{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top center;
    transition: all 3s ease-in-out;
}
.skillbtn{
    display: flex;
    margin: auto;
    justify-content: space-around;
    align-items: center;
    gap: 10px;
    color: white;
    background-color: #4070F4;
    padding: 10px;
    padding-right: 15px;
    padding-left: 15px;
    border: none;
    font-size: 18px;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.skillbtnbox{
    display: flex;
    margin: auto;
    flex-direction: column;
    justify-content: space-evenly;
    margin-top: 50px;
    width: 20%;
    margin-left: 0px;
}