.skills{
    font-size:85px;
    font-weight:700;
  
  
}


.skills span{
    color:#4070F4 ;
}
h4{
    font-size: 25px;
}
.container{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 50px;
    margin-top: 50px;
   
}

.skillsimg{
    width: 100px;
}

.skillbox{
    padding-top: 25px;
    padding-bottom: 25px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}